
.spread-chart {
  width: 500px;
  height: 230px;
  display: inline-block;
  margin-right: 20px;
  margin-bottom: 20px;
  position: relative;
}
.spread-chart .el-icon-edit {
    position: absolute;
    top: 6px;
    right: 14px;
    color: #b9b9b9;
    cursor: pointer;
}
.spread-chart .icon-wrap {
    position: absolute;
    top: 4px;
    right: 10px;
    color: #b9b9b9;
    cursor: pointer;
}
.spread-chart [class*="el-icon"] {
    font-size: 12px;
}
.spread-chart [class*="el-icon"]:hover {
      color: #06aea6;
}
.spread-chart [class*="el-icon"]:hover [class*="icon-title"] {
        display: inline-block;
}
.spread-chart [class*="el-icon"] [class*="icon-title"] {
      position: absolute;
      font-size: 10px;
      display: none;
      width: 26px;
      top: 16px;
}
.spread-chart [class*="el-icon"] .icon-title-edit {
      width: 50px;
      left: -22px;
}
.spread-chart [class*="el-icon"] .icon-title-save {
      left: -4px;
}
.spread-chart [class*="el-icon"] .icon-title-cancel {
      left: 17px;
}
.spread-chart .el-textarea {
    width: 70%;
    top: 1px;
    position: absolute;
    left: 70px;
}
.spread-chart .el-textarea .el-textarea__inner {
      text-align: center;
}



