/*fadeSmallInDown*/
@keyframes fadeSmallInDown {
from {
    opacity: 0;
    transform: translate3d(0, -80%, 0);
}
to {
    opacity: .4;
    transform: translate3d(0, 0, 0);
}
}

/*fadeOutUp*/
@keyframes fadeSmallOutUp {
from {
    opacity: .4;
    transform: translate3d(0, 0, 0);
}
to {
    opacity: 0;
    transform: translate3d(0, -80%, 0);
}
}
.scroll-enter-active {
  animation-name: fadeSmallInDown;
  animation-duration: .2s;
}
.scroll-leave-active {
  animation-name: fadeSmallOutUp;
  animation-duration: .2s;
}
.content {
  position: absolute;
  left: 180px;
  right: 0px;
  top: 76px;
  bottom: 0px;
  padding: 25px;
  overflow: overlay;
  background-color: #f5f6f6;
  box-shadow: inset 1px 0 4px 0 rgba(202, 202, 202, 0.5);
}
.content .scroll-to-up {
    background-color: #06aea6;
    position: fixed;
    right: 80px;
    bottom: 160px;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    cursor: pointer;
    opacity: .4;
}
.content .scroll-to-up:hover {
      opacity: 1;
      transition: opacity .2s;
}
.content .scroll-to-up i {
      color: #fff;
      display: block;
      line-height: 50px;
      text-align: center;
      font-size: 22px;
}



