
.entry-step-2 {
  margin-bottom: 40px;
}
.entry-step-2 .button-wrap {
    width: 95%;
    min-width: 710px;
    margin: 13px auto;
}
.entry-step-2 .el-icon-information {
    margin-left: 20px;
    color: rgba(0, 0, 0, 0.43);
}
.el-popover .popoverLoading {
  width: 100%;
  height: 100%;
}
.el-popover .title {
  text-align: center;
  font-size: 16px;
  margin-bottom: 20px;
  margin-top: 10px;
}
.el-popover .el-checkbox-group {
  padding-left: 50px;
}
.el-popover .el-checkbox-group .el-checkbox {
    display: block;
    margin-left: 15px;
    margin-bottom: 15px;
}
.el-popover .el-checkbox-group .el-checkbox .checkbox-title {
      display: inline-block;
      vertical-align: middle;
      margin-left: 10px;
}
.el-popover .button-handle {
  text-align: right;
  padding-right: 10px;
  margin-top: 25px;
  margin-bottom: 10px;
}
.info-popover {
  z-index: 2000 !important;
}
.hot .ht_master > .wtHolder {
  overflow-y: hidden;
  height: auto !important;
  display: inline-block;
  max-width: 100% !important;
  width: auto !important;
}
.button-wrapper .creat-excel-btn {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 125px;
  left: 0;
  border: none;
}
.button-wrapper .creat-excel-btn button {
    background: #06aea6;
    border: none;
    border-radius: 0;
    color: #fff;
    padding: 0;
    width: 100%;
    height: 100%;
}
.button-wrapper .creat-excel-btn button:hover {
      background: #68d1be;
}



