
.side-bar {
  position: absolute;
  width: 180px;
  left: 0;
  top: 76px;
  bottom: 0;
  box-shadow: 0 1px 4px 0 rgba(201, 201, 201, 0.5);
}
.side-bar .el-menu {
    height: 100%;
    background-color: #fff !important;
}
.side-bar .el-menu .el-menu-item, .side-bar .el-menu .el-submenu__title {
      padding-left: 32px !important;
      font-size: 18px;
}
.side-bar .el-menu .el-menu-item.is-active, .side-bar .el-menu .el-submenu__title.is-active {
        background-color: #f5f5f5;
}
.side-bar .el-menu .el-menu-item:hover, .side-bar .el-menu .el-submenu__title:hover {
        background-color: #f5f5f5 !important;
}
.side-bar .el-menu .el-menu-item .el-submenu__icon-arrow, .side-bar .el-menu .el-submenu__title .el-submenu__icon-arrow {
        font-size: 14px;
}



