
html, body {
  margin: 0px;
  padding: 0px;
  font-size: 14px;
  font-family: Microsoft YaHei !important;
  height: 100%;
  width: 100%;
}
.title1 {
  display: flex;
  align-items: center;
  font-size: 16px;
  margin-bottom: 15px;
}
.title1 .line {
    display: inline-block;
    height: 20px;
    width: 4px;
    background-color: #4db3ff;
    border-radius: 2px;
    margin-right: 8px;
}
.title2 {
  display: inline-block;
  width: 100px;
  font-size: 16px;
  text-align: right;
}
.el-input__inner, .el-textarea__inner {
  border: 1px solid #ccc !important;
  color: #1f3c3d !important;
}
::-webkit-input-placeholder {
  color: #ccc !important;
}
input, textarea {
  font-family: Microsoft YaHei !important;
}
@media screen and (min-width: 1440px) {
.shen-table .el-table__body-wrapper {
    max-height: 400px !important;
}
}
@media screen and (max-width: 1441px) {
.shen-table .el-table__body-wrapper {
    max-height: 400px !important;
}
}
.el-table__body-wrapper {
  overflow: overlay !important;
}
.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.required {
  color: red;
  vertical-align: middle;
}
.tips {
  position: absolute;
  font-size: 12px;
  color: #ff4949;
  left: 102px;
  width: 250px !important;
}
.steps {
  width: 95%;
  min-width: 710px;
  margin: 0 auto;
  height: 40px;
  background-color: #fff;
  display: flex;
}
.steps .step {
    position: relative;
    background-color: #cacaca;
    margin-right: 3px;
    flex: 1;
    text-align: center;
    line-height: 40px;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
}
.steps .step:last-child {
      margin-right: 0;
}
.steps .step .arrow {
      position: absolute;
      left: 0;
      display: inline-block;
      border-left: 15px solid #fff;
      border-top: 20px solid transparent;
      border-bottom: 20px solid transparent;
}
.steps .step .arrow:before {
        content: '';
        position: absolute;
        left: -18px;
        top: -20px;
        display: inline-block;
        border-left: 15px solid #cacaca;
        border-top: 20px solid transparent;
        border-bottom: 20px solid transparent;
}
.steps .step .arrow.arrowActive:before {
        border-left: 15px solid #06aea6;
}
.steps .stepActive {
    background-color: #06aea6;
}
.entry-step {
  position: absolute;
  left: 25px;
  right: 25px;
}
.button-wrapper {
  position: fixed;
  bottom: 0;
  text-align: center;
  left: 180px;
  right: 0px;
  height: 60px;
  line-height: 60px;
  background-color: #eee;
  box-shadow: inset 1px 0 4px 0 rgba(202, 202, 202, 0.5);
  border-top: 1px solid #e5e5e5;
  z-index: 1000;
}
.vue-echarts {
  border: 1px solid #d1d1d1;
}
@font-face {
  font-family: "iconfont";
  src: url("/static/fonts/iconfont.eot");
  /* IE9*/
  src: url("/static/fonts/iconfont.eot") format("embedded-opentype"), url("/static/fonts/iconfont.ttf") format("truetype"), url("/static/fonts/iconfont.svg") format("svg");
  /* iOS 4.1- */
}
.iconfont {
  font-family: "iconfont" !important;
  font-size: 27px;
  margin-right: 5px;
  font-style: normal;
}
.icon-logo:before {
  content: "\e60d";
  font-size: 33px;
}
.icon-exit:before {
  content: "\e61d";
  font-size: 19px;
  vertical-align: middle;
}
.icon-icon:before {
  content: "\e600";
}
.icon-zanwutupian:before {
  content: "\e63c";
}
.icon-zanwutupian1:before {
  content: "\e619";
}
.icon-yishengmorentouxiang:before {
  content: "\e784";
}
button [class^="el-icon-my"], button [class*="el-icon-my"] {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-weight: 600;
  font-style: normal;
  vertical-align: text-bottom;
}
.el-icon-my-gengxin:before {
  content: "\e616";
}
.el-icon-my-chongzhi:before {
  content: "\e617";
}
.icon-yiyuan:before {
  content: "\e618";
}
.icon-dingdan:before {
  content: "\e61a";
}
.icon-qianbao:before {
  content: "\e61b";
}
.icon-yisheng:before {
  content: "\e61c";
}



