
.excel-wrap {
  width: 95%;
  margin: 0 auto;
  margin-top: 30px;
  padding: 60px 40px 40px;
  background: #ffffff;
  box-sizing: border-box;
  border: 1px solid #e5e9f1;
  box-shadow: 0 0 3px 0 #ced7e8;
  position: relative;
  text-align: center;
}
.excel-wrap .hot {
    display: inline-block;
    max-width: 100%;
    margin: 0 auto;
}
.excel-wrap table * {
    border-color: #e8e8e8 !important;
}
.excel-wrap th,
  .excel-wrap td {
    vertical-align: middle;
}
.flex-wrap {
  display: flex;
  max-width: 100%;
  overflow: hidden;
  flex-wrap: wrap;
  width: min-content;
  margin: 0 auto;
}
#hot-display-license-info {
  display: none;
}
.btn-handle-2 {
  text-align: right;
  margin: 0 auto;
  width: 100%;
}
.btn-handle-2 .line {
    display: inline-block;
    height: 16px;
    width: 2px;
    background-color: #06aea6;
    border-radius: 2px;
    margin: 0 6px;
    vertical-align: sub;
}
.btn-handle-2 .tools {
    display: flex;
    background: #ffffff;
    border: 1px solid #e8e8e8;
    width: 100%;
    height: 40px;
    margin-top: 10px;
    align-items: center;
    border-bottom: none;
    box-sizing: border-box;
    text-align: center;
}
.btn-handle-2 .tools span {
      width: 52px;
      font-size: 14px;
      color: #666666;
}
.btn-handle-2 .tools input {
      flex: 1;
      border: none;
      outline: none;
      cursor: auto;
}
td {
  position: relative;
}
td i {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: block;
    font-style: normal;
    background: #fff;
}
.block-important {
  display: block !important;
}
.params-choosed {
  position: relative;
}
.params-choosed::after {
    display: block;
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    border: 1px dashed #000;
}
.hot canvas {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 105;
  top: 0;
  left: 0;
  pointer-events: none;
}
.layer-input {
  display: none;
  position: absolute;
  z-index: 104;
  border: none;
  outline-width: 0;
  margin: 0;
  padding: 1px 5px 0 5px;
  font-family: inherit;
  line-height: 21px;
  font-size: inherit;
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.6);
  border: 2px solid #5292f7;
  resize: none;
  -webkit-text-fill-color: transparent;
  border-radius: 0;
  background-color: #fff;
  font-size: 14px;
  box-sizing: border-box !important;
  overflow: hidden;
  -webkit-padding-start: 1px;
  -webkit-padding-end: 1px;
}
.layer-input::selection {
    color: #3390ff;
    /* WebKit/Blink Browsers */
    background: #3390ff;
}
.layer-input-display {
  display: none;
  text-align: start;
  word-break: break-all;
  position: absolute;
  z-index: 105;
  border: none;
  outline-width: 0;
  margin: 0;
  padding: 1px 5px 0 5px;
  font-family: inherit;
  line-height: 21px;
  font-size: inherit;
  resize: none;
  color: #000;
  border-radius: 0;
  background-color: transparent;
  font-size: 14px;
  box-sizing: border-box !important;
  overflow: hidden;
  pointer-events: none;
  text-align-last: left;
  border: 2px solid #5292f7;
  -webkit-padding-start: 1px;
  -webkit-padding-end: 1px;
}
.layer-input-display span {
    border-radius: 6px;
}
.layer-input-display span:nth-child(7n+1) {
      background: rgba(113, 161, 230, 0.2);
}
.layer-input-display span:nth-child(7n+2) {
      background: rgba(0, 128, 0, 0.2);
}
.layer-input-display span:nth-child(7n+3) {
      background: rgba(153, 0, 204, 0.2);
}
.layer-input-display span:nth-child(7n+4) {
      background: rgba(128, 0, 0, 0.2);
}
.layer-input-display span:nth-child(7n+5) {
      background: rgba(0, 204, 51, 0.2);
}
.layer-input-display span:nth-child(7n+6) {
      background: rgba(204, 102, 0, 0.2);
}
.layer-input-display span:nth-child(7n) {
      background: rgba(204, 0, 153, 0.2);
}
.chart-wrap {
  margin-top: 18px;
  height: 275px;
  background: #FFFFFF;
  border: 1px solid #E5E9F1;
  box-shadow: 0 0 3px 0 #CED7E8;
  width: calc(50% - 10px);
  margin-right: 20px;
  box-sizing: border-box;
  position: relative;
}
.chart-wrap:nth-child(odd) {
    margin-right: 0;
}
.chart-wrap .chart-layer {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.6);
    z-index: 2;
    display: none;
}
.chart-wrap .chart-title-editor {
    position: absolute;
    opacity: 0;
    top: 10px;
    width: 60%;
    left: 0;
    right: 0;
    display: block;
    margin: auto;
    z-index: 3;
    transform: translate(0, -80%);
    display: flex;
    align-items: center;
}
.chart-wrap .chart-title-editor input {
      -webkit-appearance: none;
      background-color: white;
      background-image: none;
      box-sizing: border-box;
      color: #1f2d3d;
      display: inline-block;
      font-size: inherit;
      height: 36px;
      line-height: 1;
      width: 100%;
      border-radius: 4px;
      border: 1px solid #bfcbd9;
      border-image: initial;
      outline: none;
      padding: 3px 10px;
      transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.chart-wrap .chart-title-editor input:focus {
        outline: none;
        border-color: #20a0ff;
}
.chart-wrap .chart-title-editor .el-icon-close, .chart-wrap .chart-title-editor .el-icon-check {
      width: 36px;
      height: 36px;
      display: inline-block;
      margin: 0 5px;
      line-height: 36px;
      cursor: pointer;
      color: #06aea6;
}
.chart-wrap .chart-title-editor .el-icon-close {
      color: #aaa;
}
.chart-wrap.title-editing .chart-layer {
    display: block;
}
.chart-wrap.title-editing .chart-title-editor {
    opacity: 1;
    transform: translate(0, 0);
    transition: 0.6s;
}
.chart-wrap .confirm-message {
    text-align: center;
}
.chart-wrap .confirm-message svg {
      width: 24px;
      height: 24px;
      margin: 0 12px;
      vertical-align: middle;
}
.chart-wrap .confirm-message span {
      font-size: 14px;
      color: #333333;
      vertical-align: middle;
}



