
.entry-step-4 {
  top: 90px;
  bottom: 85px;
}
.entry-step-4 .entry-step-inner {
    position: absolute;
    top: 0;
    bottom: 0;
    overflow: auto;
    left: 0;
    right: 0;
    background: #fff;
    padding: 0 45px;
    padding-top: 30px;
}
.entry-step-4 .part {
    margin-bottom: 25px;
}
.entry-step-4 .part .title {
      font-size: 16px;
      margin-bottom: 15px;
}
.entry-step-4 .part .el-upload_tip {
      margin-left: 15px;
}
.entry-step-4 .el-upload-list__item {
    display: inline-block;
    width: 300px;
    margin-right: 20px;
}
.entry-step-4 .ql-container {
    min-height: 150px;
}
.entry-step-4 .ql-container .ql-editor {
      min-height: 150px;
      font-family: Microsoft YaHei !important;
      font-size: 14px !important;
}
.entry-step-4 .el-upload-list--picture .el-upload-list__item-thumbnail {
    width: 270px !important;
}



