
.indicator-lib {
  width: 100%;
  box-sizing: border-box;
}
.indicator-lib > section {
    background: #ffffff;
    border: 1px solid #e5e9f1;
    box-shadow: 0 0 3px 0 #ced7e8;
    margin-bottom: 20px;
    padding: 20px 0;
}
.indicator-lib .form-wrap {
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.indicator-lib .handle-wrap {
    margin-bottom: 15px;
    width: 50%;
}
.indicator-lib .handle-wrap .input-title {
      display: inline-block;
      width: 72px;
      text-align: right;
}
.indicator-lib .handle-wrap .el-select,
    .indicator-lib .handle-wrap .small-input {
      width: 220px;
}
.indicator-lib .handle-wrap .large-input {
      width: 520px;
}
.indicator-lib .table-title {
    text-align: center;
    font-size: 18px;
    height: 70px;
    line-height: 70px;
    background-color: #fff;
    font-weight: bold;
}
.indicator-lib .handle-table {
    text-align: right;
}
.indicator-lib .handle-table .el-button {
      color: #333;
      margin: 0px 5px;
}
.indicator-lib .handle-table .line {
      width: 1px;
      height: 15px;
      background-color: #333;
      display: inline-block;
      vertical-align: text-bottom;
}
.indicator-lib .el-dialog--small {
    width: 25%;
    min-width: 345px;
}
.indicator-lib .el-table {
    border: none !important;
}
.indicator-lib .el-table .el-table__header-wrapper th {
      background-color: #f5f5f5;
}
.indicator-lib .el-table .el-table__header-wrapper th div {
        background-color: #f5f5f5;
}
.indicator-lib .el-table .el-table__body-wrapper td {
      border-bottom: none !important;
}
.indicator-lib .el-table .el-table__body-wrapper tr:nth-child(2n) td {
      background-color: #f5f5f5;
}
.indicator-lib .el-table .el-table__body-wrapper tr:nth-child(2n + 1) td {
      background-color: #fff;
}
.indicator-lib .el-table:before {
      height: 0 !important;
}
.indicator-lib .el-table:after {
      width: 0 !important;
}
.indicator-lib .el-pagination {
    height: 40px;
    background-color: #fff;
    padding-top: 20px;
    text-align: center;
}
.indicator-lib .el-pagination .el-pagination__editor {
      padding: 0 2px;
      border: none;
}



