
.charts-box{
    width:100%;
    height:100%;
    text-align:left;
    position: relative;
}
.charts-box .charts-display{
    width:100%;
    height:100%;
}
.charts-box canvas{
    width: 100%;
    height: 100%;
}
.charts-controller{
    display: inline-block;
    position: absolute;
    right: 20px;
    top: 20px;
    z-index: 1;
}
.controller-icons {
    display: inline-block;      
    height: 4px;
    line-height: 4px;
    cursor: pointer;
}
.controller-icons i{
    display: inline-block;
    width: 4px;
    height: 4px;
    border-radius: 50%; 
    background: #D8D8D8;
    margin-left: 3px;
    vertical-align: top;
}
.charts-controller ul{  
  display: none;
  position: absolute;
  /* overflow: hidden; */
  background: #ffffff;
  border: 1px solid #e8e8e8;
  box-shadow: 0 0 3px 0 #CCCCCC;
  -webkit-padding-start: 0;
  right: 0;
  top: 8px;
}
.charts-controller li{  
  width: 95px;
  height: 36px;
  line-height: 36px;
  display: inline-block;
  cursor: pointer;
  text-align: center;
  position: relative;
  z-index: 2;
  background: #fff;
  font-size: 1px;
  color: #666666;
}
.charts-controller li:hover{
  background: #FFFBE0;
  color: #333333;
}
.charts-controller:hover ul{
  display: inline-block;
}
.charts-controller ul::before{
  content: '';
  position: absolute;
  width: 10px;
  height: 10px;
  display: block;
  top: -6px;
  right: 11px;
  border: 1px solid #e8e8e8;
  border-right: none;
  border-bottom: none;
  background: #fff;
  z-index: 1;
  transform: rotate(60deg) skew(30deg);
  box-shadow: 0 0 3px 0 #CCCCCC;
}



