
.gray-wrap {
  width: 100%;
  height: 100%;
  padding: 0px;
  margin: 0px;
  background-color: #f0f0f0;
}
.gray-wrap .preview {
    width: 1000px;
    margin: 0 auto;
    padding: 0px 50px;
    height: 100%;
    background-color: #fff;
}
.gray-wrap .preview .header {
      font-size: 22px;
      text-align: center;
      margin-bottom: 30px;
      position: relative;
}
.gray-wrap .preview .header .print-btn {
        position: absolute;
        right: 0;
        top: 0;
        font-size: 16px;
        text-decoration: underline;
        cursor: pointer;
}
.gray-wrap .preview .body {
      font-size: 16px;
}
.gray-wrap .preview .body .paragraph {
        margin-bottom: 20px;
}
.gray-wrap .preview .body .paragraph .content1 {
          display: inline-block;
          width: 888px;
          vertical-align: text-top;
}
.gray-wrap .preview .body .paragraph .nav {
          padding-left: 19px;
          margin-top: 20px;
}
.gray-wrap .preview .body .paragraph .shen-table {
          width: 90%;
          margin: 0px auto;
          overflow: auto;
}
.gray-wrap .preview .body .paragraph .shen-table .table-title {
            text-align: center;
            font-weight: normal;
            margin-bottom: 20px;
}
.gray-wrap .preview .body .paragraph .shen-table .table-header, .gray-wrap .preview .body .paragraph .shen-table .table-body {
            border-collapse: collapse !important;
            width: 100%;
            box-sizing: border-box;
            border: 1px solid #dfecec;
}
.gray-wrap .preview .body .paragraph .shen-table .table-header th, .gray-wrap .preview .body .paragraph .shen-table .table-header td, .gray-wrap .preview .body .paragraph .shen-table .table-body th, .gray-wrap .preview .body .paragraph .shen-table .table-body td {
              box-sizing: border-box;
              border: 1px solid #dfecec;
              padding: 5px;
              min-width: 80px;
              text-align: center;
              font-weight: normal !important;
}
.gray-wrap .preview .body .paragraph .editors {
          display: inline-block;
          width: 890px;
          vertical-align: text-top;
}
.gray-wrap .preview .body .paragraph .editors p {
            margin-top: 0;
            margin-bottom: 10px;
            line-height: 1.5;
}
.gray-wrap .preview .body .paragraph .echarts {
          width: 700px;
          height: 300px;
          margin: 0 auto;
}
.gray-wrap .preview .body .paragraph .pdf-pagination {
          padding-top: 40px;
}
.gray-wrap .preview .body .paragraph img {
          width: 400px;
          display: block;
          margin-bottom: 15px;
}
.pagination {
  page-break-after: always;
}



