
.entry-step-1 {
  top: 90px;
  bottom: 0;
  background-color: #fff;
  padding-top: 32px;
  padding-bottom: 24px;
  overflow: overlay;
  border: 1px solid #e5e9f1;
  box-shadow: 0 0 3px 0 #ced7e8;
}
.entry-step-1 .qulity-form {
    margin-left: 92px;
}
.entry-step-1 .title-textarea {
    vertical-align: top;
    margin-top: 4px;
}
.entry-step-1 .input-wrap {
    position: relative;
    margin-top: 25px;
}
.entry-step-1 .input-wrap .el-select {
      width: 216px;
}
.entry-step-1 .input-wrap .large-input {
      width: 717px;
}
.entry-step-1 .input-wrap .bind-data {
      display: inline-block;
      width: 240px;
      word-wrap: break-word;
}
.entry-step-1 .el-input.warning .el-input__inner,
  .entry-step-1 .el-input.warning .el-textarea__inner,
  .entry-step-1 .el-select.warning .el-input__inner,
  .entry-step-1 .el-select.warning .el-textarea__inner,
  .entry-step-1 .el-textarea.warning .el-input__inner,
  .entry-step-1 .el-textarea.warning .el-textarea__inner {
    border-color: #ff4949 !important;
    box-shadow: 0 0 3px 0 #f36969;
    transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.entry-step-1 .definitions-list {
    display: inline-block;
    width: 600px;
    vertical-align: top;
}
.entry-step-1 .definitions-list .definition-item {
      margin-bottom: 25px;
}
.entry-step-1 .definitions-list .definition-item:last-child {
        margin-bottom: 0;
}
.entry-step-1 .percentage {
    width: 65px !important;
    display: inline-block;
}
.entry-step-1 .el-input__inner {
    height: 33px !important;
}
.entry-step-1 .row-bg {
    padding: 20px 30px;
    background-color: #f6f8f8;
    width: 717px;
    border-radius: 2px;
    justify-content: flex-start;
    flex-wrap: wrap;
}
.entry-step-1 .row-bg .definition-item {
      width: 100%;
}
.entry-step-1 .row-bg .large-input {
      width: 86.5%;
}
.entry-step-1 .row-bg .tips {
      left: 0;
}
.entry-step-1 .formula-input {
    background: #ffffff;
    border: 1px solid #e8e8e8;
    border-radius: 3px;
    width: 657px;
    height: 30px;
    line-height: 30px;
    margin-bottom: 10px;
}
.entry-step-1 .tips {
    margin-top: 10px;
    position: relative;
}
.entry-step-1 .primary-btn {
    margin-left: 100px;
    margin-top: 34px;
    background: #06aea6;
    border-color: transparent;
}
.entry-step-1 .primary-btn:hover {
      background: #68d1be;
}
.el-select-dropdown__list {
  padding: 0;
}
.el-select-dropdown__item.hover {
  background: #fffbe0;
  border-radius: 2px;
}



